import { useState } from 'react';

import i1 from './images/1.JPG';
import i2 from './images/2.JPG';
import i3 from './images/3.JPG';
import i4 from './images/4.JPG';

const data = [
  {
    img: i1,
    text: 'Oboohitch loh',
    bgColor: 'bg-red-500',
  },
  {
    img: i2,
    text: 'Oboohitch loh',
    bgColor: 'bg-yellow-500',
  },
  {
    img: i3,
    text: 'Oboohitch loh',
    bgColor: 'bg-blue-500',
  },
  {
    img: i4,
    text: 'Oboohitch loh',
    bgColor: 'bg-green-500',
  },
];

function App() {
  console.log();
  const [active, setActive] = useState(-1);
  return (
    <div className='bg-green-200 w-full h-screen flex justify-center items-center flex-col'>
      <div className='w-10/12 h-125 flex'>
        {data.map((item, index) => (
          <div
            className={`flex items-center text-2xl text-white justify-center transition-all duration-300 h-full ${
              item.bgColor
            } ${
              active === -1 ? 'w-1/4' : active === index ? 'w-9/12' : 'w-1/12'
            }`}
            onClick={() => setActive(index === active ? -1 : index)}
          >
            {active === index ? (
              item.text
            ) : (
              <img src={item.img} width='128' height='128' />
            )}
          </div>
        ))}
      </div>
    </div>
  );
}

export default App;
